import React from 'react';
import { Redirect} from 'react-router-dom';
import {List, InputItem,Radio, WingBlank, WhiteSpace, Button} from 'antd-mobile'
import {connect} from 'react-redux';
import {regisger} from '../store/user.redux';
import Logo from './logo/logo'

@connect(
    state => state.user,
    {regisger}
)
class Register extends React.Component{
    constructor(props){
        super(props)
        this.state={
            user:'',
            pwd:'',
            repeatpwd:'',
            type:'boss'
        }
        this.handleRegister = this.handleRegister.bind(this)
    }
    handleChange(key,val){
        console.log(key,val)
        this.setState({
            [key]:val
        })
        console.log(key,val)
        console.log(this.state)
    }
    handleRegister(){
        this.props.regisger(this.state)
    }
    render(){
        const RadioItem = Radio.RadioItem
        return (
            <div>
                {this.props .redirectTo? <Redirect to={this.props .redirectTo} />:null}
                <Logo />
                <List>
                    {this.props .msg?<p className='error-msg'>{this.props .msg}</p>:null}
                    <InputItem onChange={v=>this.handleChange('user',v)}></InputItem>
                    <WhiteSpace />
                    <InputItem type='password' onChange={v=>this.handleChange('pwd',v)}></InputItem>
                    <WhiteSpace />
                    <InputItem type='password' onChange={v=>this.handleChange('repeatpwd',v)}></InputItem>
                    <WhiteSpace />
                    <RadioItem
                        checked={this.state.type==='genius'}
                        onChange={()=>this.handleChange('type','genius')}
                    ></RadioItem>
                    <RadioItem
                        checked={this.state.type==='boss'}
                        onChange={()=>this.handleChange('type','boss')}
                    >BOSS</RadioItem>
                    <WhiteSpace />
                    <Button type='primary' onClick={this.handleRegister}> </Button>
                </List>
            </div>

        )
    }
}
export default Register


